.intro__wrapper {
  background-image: url(../img/intro-triangle.svg), url(../img/jpg/back-mobile.jpg);
  background-repeat: no-repeat;
  background-position: 0px 332px, 0px 0px;
  height: 389px;
  text-align: center;
}

.intro__image {
  margin-top: 90px;
}

.intro__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  height: 80px;
  margin: 0;
  padding: 0;
  margin-bottom: 35px;
}

.intro__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
  text-align: center;
  height: 42px;
  margin: 0;
  padding: 0;
}

.intro__subject {
  margin: 0;
  padding: 0;
  padding: 32px 20px 16px 18px;

}
